@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://fonts.googleapis.com/css?family=Oxygen);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    background: #f2f2f2;
    font-family: 'Open Sans', sans-serif;
}

.wrap {
    width: 50%;
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.scrape {
    width: 100%;
    position: relative;
    display: flex;  
}

.scrapeBar {
    width: 100%;
    border: 3px solid #9c95a0;
    border-right: none;
    padding: 5px;
    height: 50px;
    border-radius: 5px 0 0 5px;
    outline: none;
    color: #757575;
    font-size: 28px;
}

.scrapeBar:focus {
    color: #020202;
}

.scrapeBar:hover {
    color: #020202;
}

.scrapeButton {
    width: 70px;
    height:66px;
    border: 1px solid #9c95a0;
    background: #9c95a0;
    color: #fff;
    border-radius: 0 5px 5px 0;
    cursor: pointer;
    font-size: 30px;
    padding-top: 5px;
    padding-left: 6px;
}

.title {
    color: #9a8ea1;
    font-size: 90px;
    position: absolute;
    top: 37%;
    left: 50%;
    transform: translate(-50%, -50%);
    word-spacing: 20px;
    font-family: 'Oxygen', sans-serif;
}

